const dec2hex = (dec) => ('0' + dec.toString(16)).substr(-2);
export function hexId(length = 12) {
    var arr = new Uint8Array(length / 2);
    window.crypto.getRandomValues(arr);
    return Array.from(arr, dec2hex).join('');
}
export function htmlEncode(str) {
    return str.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;').replace(/'/g, '&#x27;').replace(/\//g, '&#x2F;');
}
export function hashString(s) {
    let h = 0;
    for (let i = 0; i < s.length; i++)
        h = Math.imul(31, h) + s.charCodeAt(i) | 0;
    return h;
}
export function stringToColor(str) {
    const h = Math.abs(hashString(str));
    return [h % 256, (h >> 8) % 256, (h >> 16) % 256];
}
export function clamp(a, b, c) {
    return Math.max(a, Math.min(b, c));
}
export function clampedLerp(a, b, c) {
    if (c < 0) {
        return a;
    }
    else if (c > 1) {
        return b;
    }
    else {
        return lerp(c, a, b);
    }
}
export function lerp(a, b, c) {
    return b + a * (c - b);
}
export function lerp2(a, b, c, d, e, f) {
    return lerp(b, lerp(a, c, d), lerp(a, e, f));
}
export function lerp3(a, b, c, d, e, f, g, h, i, j, k) {
    return lerp(c, lerp2(a, b, d, e, f, g), lerp2(a, b, h, i, j, k));
}
export function smoothstep(x) {
    return x * x * x * (x * (x * 6 - 15) + 10);
}
