const event = (category, action, label) => ga('send', 'event', category, action, label);
const dimension = (index, value) => ga('set', `dimension${index}`, value);
export const Tracker = {
    pageview: (target) => {
        ga('set', 'page', target);
        ga('send', 'pageview');
    },
    setTheme: (theme) => event('Generator', 'set-theme', theme),
    setVersion: (version) => event('Generator', 'set-version', version),
    setPreview: (name) => event('Preview', 'set-preview', name),
    setLanguage: (language) => event('Generator', 'set-language', language),
    reset: () => event('Generator', 'reset'),
    undo: (hotkey = false) => event('Generator', 'undo', hotkey ? 'Hotkey' : 'Menu'),
    redo: (hotkey = false) => event('Generator', 'redo', hotkey ? 'Hotkey' : 'Menu'),
    copy: () => event('JsonOutput', 'copy'),
    download: () => event('JsonOutput', 'download'),
    share: () => event('JsonOutput', 'share'),
    toggleErrors: (visible) => event('Errors', 'toggle', visible ? 'visible' : 'hidden'),
    hidePreview: () => event('Preview', 'hide-preview'),
    toggleMinimize: (minimized) => event('Generator', 'toggle-minimize', minimized ? 'minimized' : 'unminimized'),
    loadPreset: (preset) => event('Generator', 'load-preset', preset),
    dimTheme: (theme) => dimension(1, theme),
    dimVersion: (version) => dimension(3, version),
    dimLanguage: (language) => dimension(4, language),
    dimPreview: (preview) => dimension(5, preview),
};
