import { Octicon } from './Octicon';
export const Dropdown = (view, icon, entries, state, watcher) => {
    const dropdown = view.register(el => {
        el.addEventListener('change', () => {
            state.set(el.value);
        });
        state.watchRun(v => el.value = v, 'dropdown');
    });
    return `
  <div class="dropdown">
    <select data-id="${dropdown}">
      ${entries.map(e => `
        <option value=${e[0]}>${e[1]}</option>
      `).join('')}
    </select>
    ${Octicon[icon]}
  </div>`;
};
